.card-small {
    padding: 15px;
    border-radius: $radiusBig;
    background-color: $white; }

.card-outline {
    border: 1px solid $extra-light-grey;
    border-radius: $radius;
    text-align: left;
    &__header {
        line-height: 1.25;
        padding: 10px 15px;
        border-bottom: 1px solid $extra-light-grey; }
    &__body {
        padding: 15px; } }

.image-card {
    width: 300px;
    height: 350px;
    padding: 20px;
    @include cover;
    // margin: 15px
    border-radius: 15px;
    position: relative;
    color: $white;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    &__title {
        font-size: 25px;
        font-weight: 500;
        line-height: 1.2; }
    &__author {
        font-size: 0; }
    &__avatar {
        display: inline-block;
        vertical-align: middle; }
    &__name {
        display: inline-block;
        vertical-align: middle;
        margin-left: 10px;
        font-size: 15px;
        font-weight: 500; } }

.grid {
    margin: 0 auto;
    display: inline-block;
    width: 960px;
    @include sm {
        width: 300px; } }

.card {
    width: 222px;
    margin-bottom: 28px;
    &_full-width {
        width: 100%; }
    &_add {
        .card__image {
            height: 200px;
            background-color: $light-bg;
            position: relative; }
        .btn-add {
            @include vcenter; } }
    @include sm {
        width: 144px;
        margin-bottom: 20px; }
    &__image {
        max-width: 100%;
        display: block;
        border-radius: $radiusBig;
        margin-bottom: 8px; }
    &__descr {
        font-size: 13px;
        line-height: 1;
        color: $grey;
        font-weight: 500; } }

.gutter-sizer {
    width: 24px;
    @include sm {
        width: 10px; } }
