.product-page {
    margin-bottom: 70px;
    &__main {
        position: relative;
        margin-top: 40px;
        margin-bottom: 70px;
        @include clr; }
    &__slider {
        float: left;
        width: 540px; }
    &__details {
        float: left;
        width: calc(100% - 540px);
        padding-left: 40px; }
    &__close {
        text-align: right; }
    &__title {
        font-size: 20px;
        line-height: 1.25;
        font-weight: 400; }
    &__price {
        font-weight: 700;
        font-size: 20px; }
    &__info {
        margin-bottom: 24px;
        margin-top: 12px; }
    &__buttons {
        margin-top: 24px;
        font-size: 0;
        & > * {
            margin-right: 10px; } }
    &__followers {
        margin-top: 24px;
        margin-bottom: 24px; }
    &__cards-title {
        font-size: 20px;
        font-weight: 500;
        line-height: 1.5;
        margin-bottom: 30px; }
    &__cards {
        margin: 0 auto;
        text-align: center; } }

.views-details {
    color: $grey;
    font-size: 13px;
    line-height: 1.23; }
